.Home{
    padding:0;
    margin:0;
    height: 100%;
    width: 100vw;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
}
html, body {
    height: 100%;
    width: 100%;
}

*{
    box-sizing: border-box;
}
body{
    padding:0;
    margin:0;
}


.Main{
    color:whitesmoke;

margin: auto;
border-radius: 25px;


font-size: 25px;
}
.SText{
    font-size: 30px;
    margin-top: 200px;
    width: 50%;
    margin: auto;
    display:inline-block;
bottom:5%;
left:5%;
}
.About{
    color:whitesmoke;
    font-size: 25px;
    padding: 20px;
    border-radius: 25px;
    width: 80%;
    margin: auto;
    margin-top: 100px;

}

.first-section{
    width:100vw;
    height:min(100vw,800px);
    position: relative;
    margin:0;
    background-color: rgba(15, 15, 15, 0.7);
    display:flex;
    justify-content:center;
    align-items:flex-start;
    box-shadow:3px 3px 10px 3px var(--primary-nav);

}
.Skills{
    display: flex;
        border:solid 2px var(--secondary-color);
    justify-content:center;
    background-color: rgba(17, 15, 15, 0.7);
    box-shadow:3px 3px 10px 3px var(--primary-nav);
    border:solid 2px var(--secondary-color);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:whitesmoke;
    margin-top: 100px;
    width: min(500px,80vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}
.badge{
    width:10rem;
    height :3rem;
    background-color : var(--secondary-color-50);
    position: absolute;
    top:0;
    right:0;
    transform-origin:center;
    transform:rotateZ(45deg) translate(3rem,-3rem);
    overflow:hidden;
    text-align:center;
    font-size: 30px;
}
.Skills ul {
    text-decoration: none;
    text-align: center;
    font-size:20px;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content:center;
}
.Skills li{
    font-weight : bold;
    background-color: var(--secondary-color-50);
    border:var(--secondary-color) solid 3px;
    border-radius: 50%;
    height:150px;
    width:150px;
    display:flex;
    justify-content:center;
    align-items: center;
    margin:10px;
    overflow-wrap: break-word;
}
.Skills span{
    
}
.openMenu{
  width: 35px;
  height: 5px;
  background-color: var(--secondary-nav);
  margin: 6px 0;
  transition: .15s;
}
.menu-bar-icon {
    color:var(--secondary-nav)
}
.menu-bar-icon :hover ,.menu-bar-icon:active{
    opacity : 0.8;
    background-color:whitesmoke;
    
}
.About{
    display: flex;
    justify-content:center;
    background-color: rgba(17, 15, 15, 0.7);
    box-shadow:3px 3px 10px 3px var(--primary-nav);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:whitesmoke;
    margin-top: 100px;
    width: min(500px,80vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    border:solid 2px var(--secondary-color);
}
.About:hover{
    background-color:var(--secondary-nav-50);
    border:solid 5px var(--secondary-nav-50);
}
.Skills:hover{
    background-color:var(--secondary-nav-50);
    border:solid 5px var(--secondary-nav-50);
}
.About ul{
    text-decoration: none;
    text-align: left;
    font-size:20px;
    
    justify-content:center;
}
.About li{
    font-weight: bold;
}
@media(max-width:600px)
{
 .SText{
    width: 100%;
    display: block;
 }   
 .first-section{
    flex-direction: column;
    justify-content:center;
    align-items:center;
 }
}
.header{
    font-weight: bold;
    text-shadow: 3px 3px 3px var(--secondary-color)d;
}
html{
    background-image: url('./bg2.jpg');
    background: rgb(5,5,5);
    
    background: radial-gradient(circle, rgba(5,5,5,1) 6%, rgba(5,5,5,1) 15%, rgba(5,5,5,1) 20%, rgba(5,5,5,1) 22%, rgba(27,154,160,1) 41%, rgba(27,154,160,1) 42%, rgba(0,0,0,1) 74%, rgba(5,5,5,1) 85%)
    ,url('./bg2.jpg')
    ;
    background-attachment: fixed;
   
    background-repeat: round;
    background-blend-mode:screen;
}
